#viewer-div {
    width: 100%;
    height: 100%;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

#viewer-div > #header-div {
    height: 5%;
    display: table;
}

#viewer-div > #img-div {
    height: 95%;
    width: 100%;
    position: relative;
    overflow: hidden;
}

#viewer-div > #header-div > button {
    color: var(--maincolor);
}

#viewer-div > #header-div > p {
    text-align: center;
    vertical-align: middle;
    display: table-cell;
    padding-right: 8px;
}

#viewer-div > #img-div > img {
    object-fit: contain;
    height: 100%;
    width: 100%;
}

#viewer-div > #img-div > #scratcher-div {
    position: absolute;
    width: 50px;
    height: 50px;
    z-index: 999;
    animation: MoveUpDown 1s linear infinite;
}

#viewer-div > #img-div > #scratcher-div > img {
    width: 50px;
    height: 50px;
    z-index: 999;
    display: block;
}

@keyframes MoveUpDown {
    0%, 100% {
        transform: translateY(0);
    }
    50% {
        transform: translateY(-10px);
    }

}
